body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  padding: 0px;
  margin: 0px;
}

.nav_container{
  display: inline-block;
  position: fixed;
  top: 24px;
  left: 24px;
  padding: 8px;
  z-index: 1000;
}

.nav_image{
  position: relative;
  width: 96px;
  height: 96px;
  left: 8px;
  object-fit: cover;
  border-radius: 50%;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);  
  -webkit-transition: -webkit-transform 0.1s ease-in;  
  transition: -webkit-transform 0.1s ease-in;  
  transition: transform 0.1s ease-in;  
  transition: transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
}

.nav_image_container {
  width: 96px;
  height: 96px;
  border-radius: 50%; 
  overflow: hidden;
}

.nav_image_container:hover .nav_image{
  -webkit-transform: scale(2.0);
          transform: scale(2.0);
}

.nav_link{
  display: inline-block;
  position: absolute;
  top: 48px;
  left: 24px;
  color: white;
  background-color: #8c5b8d;
  padding: 0px 8px;
  font-family: 'Fresca', cursive;
  border-radius: 40%;
  -webkit-transition: left 0.24s ease-in;
  transition: left 0.24s ease-in;
  letter-spacing: 1px;
}

.nav_image_container:hover .nav_link{
  left: 128px;
}

@media only screen and (max-width: 992px){
  .nav_container{
    display: block;
    position: static;
    position: initial;
    top: 0px;
    left: 8px;
    padding: 8px;
  }

  .nav_image{
    -webkit-transform: scale(2.0);
            transform: scale(2.0);
  }

  .nav_link{
    left: 128px;
  }
}
