.c-loader{
    height: 30px;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
}

.c-loader-item{
    height: 30px;
    width: 30px;
    border: 20px;  
    display: inline-block;
    background: #e1e2e3;
    /* margin: 2px; */
    -webkit-transform: scale(0.0);
            transform: scale(0.0);
    border-radius: 50px;
    -webkit-animation: grow 1.5s infinite;
            animation: grow 1.5s infinite;
    margin: -15px;
}

.c-loader-item:nth-child(2){
    background: #add1f3;
    -webkit-animation-delay: 0.75s;
            animation-delay: 0.75s;
}

/* .c-loader-item:nth-child(2){
    animation-delay: 1.0s;
} */

@-webkit-keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes grow{
    50% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}

@keyframes grow{
    50% {
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}


:root{ 
    --color-main: #673ab7;
    --color-mid: #5c6bc0;
    --color-sec: #26a69a;
    --background-main: linear-gradient(to right, var(--color-main), var(--color-mid), var(--color-sec));
    --text-background: #FFFFFF;
    --text-color-normal: #323232;

    --text-normal: 'Varela Round', 'sans-serif';
    --text-code: 'fantasque sans mono', consolas, 'ubuntu mono', monospace;
    --text-header: 'Varela Round', 'sans-serif';
}

.article-view-title{
    font-family: Righteous, 'sans-serif';
    font-size: 64px;
    font-weight: bold;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 1.0em;
}

.article-item-details{
    font-family: 'Varela Round', 'sans-serif';
    font-family: var(--text-normal);
    font-size: 12px !important;
    text-transform: uppercase;
}

.article-tag{
    background-color: black;
    border-radius: 10px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 8px;
    padding-right: 8px;
    font-size: 12px;
    font-weight: 100;   
}

.article-text-container {
    border-radius: 4px;
}

.article-text-container p{
    /* line-height: 1.8em; */
    font-size: 18px;
    padding: 0px 8px;
    font-family: 'Varela Round', 'sans-serif';
    font-family: var(--text-normal);
}

.article-text-container p > img {
    border-radius: 4px;
    position: relative;
    left: 50%;
    border: 1px solid gray;
    margin-top: 48px;
    margin-bottom: 48px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 100%;
}

.article-text-container h1, .article-text-container  h2, .article-text-container  h3, .article-text-container  h4, .article-text-container h5, .article-text-container h6{
    margin: 24px 0px;
    font-family: 'Varela Round', 'sans-serif';
    font-family: var(--text-header);
    padding: 2px 8px;
    border-radius: 8px;
    padding: 0px 8px;
    font-weight: bold;
    letter-spacing: 1px;
    color: #5c6bc0;
    color: var(--color-mid);
}

.article-text-container strong {
    padding: 2px 4px 0px 4px;
    font-size: 16px;
    color: #323232;
    color: var(--text-color-normal);
    font-weight: normal;
    border-radius: 4px;
    position: relative;
    overflow: hidden;
}

.article-text-container strong::before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    background: -webkit-gradient(linear, left top, right top, from(#673ab7), color-stop(#5c6bc0), to(#26a69a));
    background: -webkit-linear-gradient(left, #673ab7, #5c6bc0, #26a69a);
    background: linear-gradient(to right, #673ab7, #5c6bc0, #26a69a);
    background: var(--background-main);
    z-index: -1;
    opacity: 0.6;
}

.article-text-container ul, ol{
    font-family: 'Varela Round', 'sans-serif';
    font-family: var(--text-normal);
    font-size: 18px;
    margin-top: 12px;
    padding: 0px 8px;
    word-wrap: break-word;
}

.article-text-container code{    
    font-family: 'fantasque sans mono', consolas, 'ubuntu mono', monospace !important;    
    font-family: var(--text-code) !important;
    font-size: 1.1em;
}

.article-text-container pre {
    border-radius: 12px;
    overflow: hidden;
}

.article-text-container pre::-webkit-scrollbar{
    display: none;
}

.article-text-container th, .article-text-container thead, .article-text-container td{
    font-family: 'Varela Round', 'sans-serif';
    font-family: var(--text-normal);
}

.article-text-container thead {
    color: #5c6bc0;
    color: var(--color-mid);
}

.article-text-container th {
    padding: 12px 12px 4px 12px;
    font-size: 16px;
}

.article-text-container td {
    padding: 4px 12px;
    font-size: 16px;
}

.article-text-container table {
    border-radius: 1em;
    border-collapse: collapse;
    overflow: hidden;
    position: relative;
    z-index: 1;
    background: #eaeaea;
    margin-top: 16px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.article-text-container table::before {
    content: "";
    z-index: -1;
    opacity: 0.2;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, right top, from(#673ab7), color-stop(#5c6bc0), to(#26a69a));
    background: -webkit-linear-gradient(left, #673ab7, #5c6bc0, #26a69a);
    background: linear-gradient(to right, #673ab7, #5c6bc0, #26a69a);
    background: var(--background-main);
}

@media only screen and (max-width : 992px) {
    .article-view-title{
        font-size: 48px;
    }

    .article-item-details {
        font-size: 16px;
    }

    .article-tag{
        font-size: 16px;
    }
}

@media only screen and (max-width : 768px) {
    .article-text-container p{
